
body {
  background-color: #f5f5f5;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
header {
  color: #2196f3;
  font-size: 70px;
  text-align: center;
  width: 100%;
  height: 150px;
  line-height: 150px;
  font-style: italic;
  font-weight: 700;
}
.box-shadow {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  background: #fff;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.todo-add-item {
  width: calc(100%);
  border: 0;
  padding: 0 10px;
  outline: 0;
  font-size: 32px;
}
.icon-size {
  height: 32px;
  min-height: 32px;
  width: 32px;
  min-width: 32px;
  cursor: pointer;
}

.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.item-action {
  display: none;
}
.todo-list-item:hover > .view > div > .item-action {
  display: block;
  -webkit-animation: fadeIn 0.5s ease;
          animation: fadeIn 0.5s ease;
  cursor: pointer;
}
.todo-list-item > .view {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.todo-list-item > .view > div {
  width: calc(100% - 32px - 1rem);
}
.item-action {
  position: absolute;
  right: 0;
}
.icon-state-item {
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border-radius: 20px;
}
.icon-state-item:hover {
  background-color: #4baf88 !important;
}
.item-deleted > img {
  background-color: #4baf88 !important;
  opacity: 0.5;
}
.item-deleted > div > p {
  text-decoration: line-through;
  opacity: 0.5;
}
.todo-list-item.editting > .view {
  display: none;
}
.todo-list-item > .edit {
  display: none;
}
.todo-list-item.editting > .edit {
  display: block;
}
@-webkit-keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes fadeIn {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}

.sort-item {
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 115px;
  border: 1px solid #777;
}
.sort-item.active {
  border: 1px solid #007bff;
}
.sort-item:hover {
  background-color: #2196f3;
  color: #fff;
}
.count-item {
  font-weight: 700;
}
